.item {
  display: block;
  height: 10px;
  width: 10px;
  border-radius: 50%;
}

.green {
  composes: item;
  background-color: green;
}

.red {
  composes: item;
  background-color: red;
}

.avatar {
  margin-right: 15px;
  margin-left: 15px;
}

.name {
  color: black;
  font-weight: bold;
}

.info {
  display: flex;
}
